import { FlagService } from './../flag.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-submission-component',
  templateUrl: './submission-component.component.html',
  styleUrls: ['./submission-component.component.css']
})
export class SubmissionComponentComponent implements OnInit {

  
  BLANK =  {
    number : "",
    title : "",
    description : "",
    url : "",
    submitted : true,
  }
  
  ERR =  {
    number : "",
    title : "ERR",
    description : "",
    url : "",
    submitted : false,
  }

  flag: any;

  submission = {
    flag :"",
    code : ""
  }
  constructor(private service:FlagService) { 
    this.flag = this.BLANK;
  }


  ngOnInit(): void {
    var self = this;
    this.service.getFlag().then(result =>  self.setFlag(result)).catch(err=> self.flag = self.ERR);
  }


setFlag(flag){
  this.flag = flag;
  this.submission.flag = flag.number;

}

submit(){
  var self = this;
 this.service.submitFlag(this.submission).then(r=> self.submitted(r));
}

submitted(r){
  var self = this;

  this.service.getFlag().then(result =>  self.setFlag(result)).catch(err=> self.flag = self.ERR)
  alert(r);


}
}
